import Vue from 'vue'
import Router from 'vue-router'
// 
import Login from '@/components/login'
import Regin from '@/components/regin'
import AdminLogin from '@/admin/views/adminlogin'

// 
import Home from '@/components/home'
import ConHome from '@/components/con-home'
import Products from '@/components/page/products'
import FAQ from '@/components/page/FAQ'
import News from '@/components/page/news'
import Manger from '@/components/page/manger'
import NewsContent from '@/components/page/newscontent'
// 
import My from '@/components/page/manger/my'
import Send from '@/components/page/manger/send'
import MyHistory from '@/components/page/manger/history'
// 
import ProductList from '@/components/page/product/productlist'
import ProductContent from '@/components/page/product/productcontent'
// 
import NewAdminer from '@/admin/views/adminer/newadminer'
// lazyLoad components
// const lazyComp = name => () => import(`@/components/${name}`)
Vue.use(Router)
// ,
export const constantRoutes = [
  // 
  {
    path: '/',
    hidden: true,
    type: 'client',
    component: Home,
    children: [
      {
        path: '/',
        hidden: true,
        component: ConHome
      },
      {
        path: '/products',
        name: '',
        class: 'el-icon-goods',
        component: Products,
        redirect: '/product/',
        children: [
          {
            // 
            path: '/product/:class',
            component: ProductList
          }
        ]
      },
      {
        path: '/product/:class/:productname',
        hidden: true,
        component: ProductContent
      },
      {
        path: '/news/:id',
        hidden: true,
        component: NewsContent
      },
      // {
      //   path: '/localproduct',
      //   name: '',
      //   component: lazyComp('page/localProduct')
      // },
      // {
      //   path: '/localproduct/:id',
      //   hidden: true,
      //   component: lazyComp('page/localProd')
      // },
      {
        path: '/FAQ',
        name: '',
        hidden: true,
        component: FAQ
      },
      {
        path: '/news',
        name: '',
        component: News
      },
      {
        path: '/manger',
        name: '',
        redirect: '/manger/my',
        meta: {
          requireUser: true
        },
        component: Manger,
        // hasChild: true,
        children: [
          {
            path: '/manger/my',
            meta: {
              requireUser: true
            },
            name: '',
            component: My
          },
          {
            path: '/manger/send',
            name: '',
            meta: {
              requireUser: true
            },
            component: Send
          },
          {
            path: '/manger/history',
            name: '',
            meta: {
              requireUser: true
            },
            component: MyHistory
          }
        ]
      }
    ]
  },
  // 
  {
    path: '/adminlogin',
    hidden: true,
    component: AdminLogin
  },
  {
    path: '/login',
    name: '',
    hidden: true,
    component: Login
  },
  {
    path: '/regin',
    name: '',
    hidden: true,
    component: Regin
  },
  {
    path: '/addadminer',
    name: '',
    component: NewAdminer
  }
]

export default new Router({
  routes: constantRoutes,
  mode: 'history'
})



